<template>
  <div>
    <!-- <div id="local_stream"></div> -->
  </div>
</template>

<script>
// import AgoraRTC from "AgoraRTC";
import AgoraRTC from "agora-rtc-sdk";
import config from "../common/config";

// AgoraRTC.Logger.setLogLevel(AgoraRTC.Logger.NONE);
export default {
  props: {
    mode: {
      type: String,
      default: "live"
    },
    codec: {
      type: String,
      default: "h264"
    }
  },
  data() {
    return {
      client: null
    };
  },
  methods: {
    async init() {
      this.client = AgoraRTC.createClient({
        mode: this.mode,
        codec: this.codec
      });
      this.client.on("error", err => {
        console.log("client error:", err);
      });
      this.client.on("stream-added", evt => {
        console.log("stream-added");
        this.$emit("added", evt);
      });
      this.client.on("stream-subscribed", evt => {
        console.log("stream-subscribed");
        this.$emit("subscribed", evt);
      });
      this.client.on("peer-online", evt => {
        console.log("peer-online", evt);
      });
      this.client.on("peer-leave", evt => {
        console.log("peer-leave", evt);
      });
      this.client.on("stream-removed", evt => {
        console.log("stream-removed");
        this.$emit("removed", evt);
      });
      this.client.on("error", err => {
        console.log(err);
      });
      return await this.client.init(config.appid);
    },
    async join(channel, uid, token) {
      return new Promise((resolve, reject) => {
        this.client.join(
          token,
          channel,
          uid,
          streamid => {
            resolve(streamid);
          },
          error => {
            reject(error);
          }
        );
      });
    },
    createLocalStream(streamID) {
      console.log("createLocalStream => ", streamID);
      return new Promise((resolve, reject) => {
        const localStream = AgoraRTC.createStream({
          streamID,
          audio: true,
          video: true,
          screen: false
        });
        localStream.init(
          () => {
            resolve(localStream);
          },
          error => {
            reject(error);
          }
        );
      });
    },
    publish(localStream) {
      this.client.publish(localStream, error => {
        console.log("publish localstream =>", error);
      });
    },
    async subscribe(remoteStream) {
      this.client.subscribe(remoteStream, err => {
        console.log("subscribe:", err);
      });
    },
    leave() {
      if (this.client) {
        this.client.leave();
      }
    },
    async test() {
      this.client = AgoraRTC.createClient({
        mode: this.mode,
        codec: this.codec
      });
      await this.client.init(config.appid);
      this.client.join(
        null,
        "sma2lbao_room",
        "sma2lbao",
        streamID => {
          console.log(streamID);
          const localStream = AgoraRTC.createStream({
            streamID,
            audio: true,
            video: true,
            screen: false
          });
          localStream.init(
            async () => {
              console.log("play_local");
              localStream.play("local_stream", { fit: "cover" });
              console.log("play_local");
              await this.client.publish(localStream);
              console.log("----- publish localStream -----");
            },
            error => {
              console.error(error);
            }
          );
        },
        error => {
          console.log(error);
        }
      );
    },
    getDevices() {
      console.warn("RTC GetDevices => ", Date.now());
      return new Promise(resolve => {
        AgoraRTC.getDevices(devices => {
          console.warn("devices => ", devices);
          resolve(devices);
        });
      });
    },
    async getPermission() {
      /* eslint-disable */
      const tempAudioStream = AgoraRTC.createStream({ audio: true, video: false });
      const tempVideoStream = AgoraRTC.createStream({ audio: false, video: true });
      const audioPermissionOK = new Promise(resolve => {
        tempAudioStream.init(() => resolve(null), (e) => resolve(e));
      });
      const videoPermissionOK = new Promise(resolve => {
        tempVideoStream.init(() => resolve(null), (e) => resolve(e));
      });

      await Promise.all([audioPermissionOK, videoPermissionOK]).then(res => {
        if (res[0] !== null) {
          console.warn("create audio temp stream failed!", res[0]);
        }
        if (res[1] !== null) {
          console.warn("create video temp stream failed!", res[0]);
        }
      });
      if(tempAudioStream) tempAudioStream.close();
      if(tempVideoStream) tempVideoStream.close();
      // 音视频采集完成后，调用 getDevices 获取 label 和 deviceId
      let devices = await this.getDevices();
      let authCamera = false;
      let authAudio = false;
      devices.forEach(dev => {
        if(dev.kind === "videoinput" && dev.deviceId && dev.deviceId > 0) authCamera = true;
        if(dev.kind === "audioinput" && dev.deviceId && dev.deviceId > 0) authAudio = true;
      });
      /* eslint-disable */
    }
  },
  created() {
    console.log("RTCClient Created => ", new Date().toString());
  },
  async mounted() {
    console.log("RTCClient Mounted => ", new Date().toString());
    // this.getPermission();
  },
};
</script>
