// import Vue from "vue";
import VueRouter from "vue-router";
// import Home from "../views/Home.vue";
// import ChatHome from "../views/ChatHome";
// import Discover from "../views/Discover";
// import Message from "../views/Message";
// import Personal from "../views/Personal";
// import FirebaseUi from "../views/FirebaseUi";

// Vue.use(VueRouter);

const routes = [
  {
    path: "/",
    name: "home",
    component: () => import("@/views/Home"),
    redirect: "/home",
    children: [
      {
        path: "home",
        name: "chat-home",
        component: () => import("@/views/ChatHome"),
        meta: {
          title: "Home",
          keep: true
        }
      },
      {
        path: "discover",
        name: "discover",
        component: () => import("@/views/Discover"),
        meta: {
          title: "Discover",
          keep: true
        }
      },
      {
        path: "message",
        name: "message",
        component: () => import("@/views/Message"),
        meta: {
          title: "Message",
          keep: true
        }
      },
      {
        path: "personal",
        name: "personal",
        component: () => import("@/views/Personal"),
        meta: {
          title: "Personal",
          keep: true
        }
      }
    ],
    meta: {
      keep: true
    }
  },
  {
    path: "/chat-detail/:uid",
    name: "chat-detail",
    component: () =>
      import(/* webpackChunkName: "chat-detail" */ "../views/ChatDetail.vue"),
    meta: {
      keep: false
    }
  },
  {
    path: "/chatroom/:roomid",
    name: "chatroom",
    component: () =>
      import(/* webpackChunkName: "chatroom" */ "../views/ChatRoom.vue"),
    meta: {
      keep: false
    }
  },
  {
    path: "/login",
    name: "login",
    component: () =>
      import(/* webpackChunkName: "login" */ "../views/Login.vue"),
    meta: {
      title: "Login",
      keep: false
    }
  },
  {
    path: "/phone-login",
    name: "phone-login",
    component: () =>
      import(/* webpackChunkName: "phone-login" */ "../views/PhoneLogin.vue"),
    meta: {
      title: "Phone Login",
      keep: false
    }
  },
  {
    path: "/personal-edit",
    name: "personal-edit",
    component: () =>
      import(
        /* webpackChunkName: "personal-edit" */ "../views/PersonalEdit.vue"
      ),
    meta: {
      keep: false
    }
  },
  {
    path: "/recharge",
    name: "recharge",
    component: () =>
      import(/* webpackChunkName: "recharge" */ "../views/Recharge.vue"),
    meta: {
      keep: false
    }
  },
  {
    path: "/message-detail/:touid",
    name: "message-detail",
    component: () =>
      import(
        /* webpackChunkName: "message-detail" */ "../views/MessageDetail.vue"
      ),
    meta: {
      keep: false
    }
  }
  // {
  //   path: "/firebase-ui",
  //   name: "firebase-ui",
  //   component: FirebaseUi,
  //   meta: {
  //     keep: false
  //   }
  // }
];

const router = new VueRouter({
  mode: "hash",
  // base: process.env.BASE_URL,
  routes,
  scrollBehavior(to, from, savedPosition) {
    if (savedPosition && to.meta.keep) {
      if (savedPosition === null) {
        to.meta.saved_position = null;
      } else {
        savedPosition = to.meta.saved_position;
      }
      let back = savedPosition ? savedPosition : { x: 0, y: 0 };
      return back;
    } else {
      return { x: 0, y: 0 };
    }
  }
});

router.beforeEach((to, from, next) => {
  // console.log(to);
  if (from.meta.keep) {
    from.meta.saved_position = { x: window.pageXOffset, y: window.pageXOffset };
  }
  next();
});
export default router;
